import { LocalStorageService } from './../core/service/local-storage.service';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { UserSession } from 'core/sessions';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from '../core/http_wrapper/http_wrapper_response';
import { Constants } from '../core/constants';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { ConfirmpopupComponent } from 'classes/confirmationPopup/confirmationPopup.component';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import * as xlsx from 'xlsx';
import { Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter } from 'rxjs/operators';
import { ActivitiesListData } from 'core/models/activitiesListResponse';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { TrainerGymDetail } from 'core/models/gymtrainerDetailResponse';
import { CustomData } from 'core/autocomplete/custom_data';
import { BaseComponent } from 'app/base/base.component';

declare var $: any;

@Component({
  selector: 'app-booking-module',
  templateUrl: './booking-module.component.html',
  styleUrls: ['./booking-module.component.css']
})
export class BookingModuleComponent extends BaseComponent implements OnInit ,HttpSuccesFailureResponse{
  @ViewChild('bkgtable') bkgtable: ElementRef;
  trainerList=[];

  startDate: any="2017/07/10";
  endDate:any="2017/10/10";
 // headerTitleGYMRegular=[{name:"Date",view:false,id:0},{name:"Slot",view:false,id:0},{name:"Class Name",view:false,id:0},{name:"Customer Name",view:false,id:0},{name:"Customer Email",view:false,id:0},{name:"Package Type",view:false,id:0}]
  headerTitleGYMRegular=[{name:"Date",view:false,id:0},{name:"Slot",view:false,id:0},{name:"Class Name",view:false,id:0},{name:"Trainer",view:false,id:0},{name:"Customer Name",view:false,id:0},{name:"Contact info",view:false,id:0},{name:"Child Name",view:false,id:0},{name:"Package Type",view:false,id:0},{name:"Status",view:false,id:0},{name:"Action",view:false,id:0}]
  newpaymentData = [{"title":"energy class","price":0.0,"trainerName":"tison kumar","classDate":"2020-12-02","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":0,"generalVisitors":2,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false},{"title":"energy class","price":0.0,"trainerName":"tison kumar","classDate":"2020-12-01","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":0,"generalVisitors":1,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false},{"title":"energy class","price":12.0,"trainerName":"tison kumar","classDate":"2020-12-16","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":1,"generalVisitors":1,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false},{"title":"energy class","price":0.0,"trainerName":"tison kumar","classDate":"2020-12-17","fromTime":"18:00:00","toTime":"19:00:00","priceModel":"CHARGABLE","numberOfParticipants":1,"dropInVisitors":0,"generalVisitors":1,"ptVisitors":0,"workshopVisitors":0,"gymRateCardList":[],"videoClass":false}];
  userType;
  listIsLaoding;
bookingsData:any=[];
bookingsDataFromApi:any=[];
bookingsDataFromApiTotal:any=[];
searchText="";
bookingtoCancel;
currentPage = 0;
searchDecouncer$: Subject<string> = new Subject();
activitiesListDataResponse: ActivitiesListData[] = [];
 seletedSubActivityResponse=[];
 showAddSubactivity:any=false;
 subActivityDataResponse=[];
 subCategoryId: any;
 categoryId:any;
userId:any;
trainerId:any;
isMasterGym:boolean=false;
totalCount: any=0;


  ngAfterViewInit() {

    var that=this;
    $(function() {
      $('input[name="daterange"]').daterangepicker({ 
        locale: {
            format: 'YYYY/MM/DD'
        },
        startDate: that.startDate,
        endDate:  that.endDate,
        // maxDate:new Date(),
    });
  });
 

  $('input[name="daterange"]').on('apply.daterangepicker', function(ev, picker) {
that.dateRangeChange(picker.startDate._d,picker.endDate._d);
});

    $(document).ready(function() {
        window.alert = function(){};
    });

  }


  constructor(private route: ActivatedRoute,private router: Router,private http_userauth: HttpUserAuthUtilsService,private modal: Modal,    private http_common: HttpCommonUtilsService) {
    super(http_userauth);
   }
  
  ngOnInit(): void {
    this.loadAdminDataIfLoggenIn();
   // this.getBookingListTotal();
   this.isMasterGym = LocalStorageService.isMasterGym();
    if(this.isMasterGym){
      this.http_userauth.getSubCategoryList(this, false);
    }else{
      this.http_userauth.getSubCategoryList(this, false,LocalStorageService.getUserId());

      
    }
    this.userType = LocalStorageService.getUserType();
    this.showAddSubactivity=LocalStorageService.showSubActivity();
    this.http_common.getFilteredActivity(this, false);
    this.route.queryParams.subscribe(qparams=>{
      if(qparams['userId']){
      this.userId= qparams['userId'];
      }

    })
    this.route.params.subscribe(params => {
      if(params['fromDate']){
        this.startDate = UserSession.getStartDate(params['fromDate']) || null;}else{
       // this.startDate = UserSession.getStartDate(new Date());
       this.startDate = UserSession.getStartDate(new Date(
        new Date().getFullYear(),
        new Date().getMonth() - 1, 
        new Date().getDate()
    ));
      }
      if(params['toDate']){
        this.endDate = UserSession.getStartDate(params['toDate']) || null;
      }else{
        this.endDate = UserSession.getStartDate(new Date());
      }
       this.currentPage = 0;
      this.getBookingList();
      this.setupSearchDebouncer();
      });
      this.trainerListByGym();
  }
  trainerListByGym() {
    this.listIsLaoding = true;
    this.http_userauth.getAllTrainerListNewGym(LocalStorageService.getUserId(), 0, this, true)
 
}

  dateRangeChange(start,end){
    this.startDate=UserSession.getStartDate(start);
    this.endDate=UserSession.getStartDate(end);
    if(LocalStorageService.getUserType()==2){
      this.router.navigate(['trainer/bookings',this.startDate,this.endDate]);
    }else{
      this.router.navigate(['gym/bookings',this.startDate,this.endDate]);
  
  }
  this.searchText ="";
  return;
  }


  getBookingList(){
   if(this.userId){
      this.http_userauth.getGymBookingsUserWise(LocalStorageService.getUserId(),this.userId,this,true,this.trainerId);

   }else{
    this.http_userauth.getGymBookings(LocalStorageService.getUserId(),this.startDate , this.endDate,this.currentPage,this.searchText,this.categoryId,this.subCategoryId,this,true,this.trainerId);

   }
    
    }

  // getBookingListTotal(){
  //   let startDate = "2020-12-25";
  //   let endDate = UserSession.getStartDate(new Date());
  //  this.http_userauth.getGymBookingsTotal(LocalStorageService.getUserId(),startDate , endDate,this,true);
    
  //   }
  onActivitySeleted(event){
    this.seletedSubActivityResponse=[];
    let value= event.target.value;
    if(this.showAddSubactivity){
      this.subActivityDataResponse.forEach(element => {
        if(value==element.classCategoryResponseDto.id){
        this.seletedSubActivityResponse.push(element);
        }
     });
    }
    this.categoryId = value;
    this.subCategoryId = undefined;
    this.getBookingList();

  
  
  }
  selectTrainer(event){
    let value= event.target.value;
    if(value){
      this.trainerId = value;
    }else{
      this.trainerId = undefined;
    }
    this.getBookingList();
  }
  onSubActivitySelected(event){
    let value= event.target.value;
    if(value){
      this.subCategoryId = value;

    }else{
      this.subCategoryId = undefined;

    }

    this.getBookingList();




  }


    onSuccess(type: any, responsedata: any) {
      if (type === Constants.AllTrainerApi) {
        this.listIsLaoding = false;
        const temp_list: TrainerGymDetail[] = responsedata;
        temp_list.forEach(element => {
           this.trainerList.push(element);
         });
    
        
    
    
    
    }
      if (type === Constants.AllActivityListApi) {
        this.activitiesListDataResponse = UserSession.setStatusForList(responsedata.data);
      }
      if (type === Constants.getSubCategoryApi) {
        this.subActivityDataResponse = responsedata.data;
      }
      if(type==Constants.gymBookingsAPI||type==Constants.gymBookingsAPIUserWise){
        this.bookingsDataFromApi = responsedata.data;
        //this.bookingsDataFromApi.reverse();

        this.bookingsData=this.bookingsDataFromApi;
        if(this.currentPage==0){
          this.totalCount = responsedata.totalCount;
       
      }
    }

      if(type==Constants.gymBookingsAPITotal){
        this.bookingsDataFromApiTotal=responsedata.data;
      }
      if(type == Constants.unjoinUserClass){
        this.getBookingList()
      }
    }
    onFailure(type: any, response: any) {
      if(type == Constants.unjoinUserClass){
       
      }
    }

    cancelBooking(item){
      
      this.bookingtoCancel = item;
     this.openconfirmpopup("Do you really want to remove "+item.customerName+" from your class ?","Are you Sure?","With Refund","Without Refund",this,"NO_CANCELACTION")
    }
    
    openconfirmpopup(message, title,yesTitle,noTitle, parent?: any, useCustomMethod?: any) {
      return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title ,yesTitle,noTitle, parent, useCustomMethod}, BSModalContext));
  
    }
    confirmAction(){
      
    this.http_userauth.unjoinusersessiongym(this.bookingtoCancel.classJoinId,"REFUND",this,true);

    }
    cancelAction(){
      

      this.http_userauth.unjoinusersessiongym(this.bookingtoCancel.classJoinId,"NO_REFUND",this,true);

    }
    addBooking(){
      this.router.navigate(['addBookingModule']);
    }
    onFilterApply(event)
    {
    //  let  localBookingData:any=[];
    //  if(this.searchText&&this.searchText.length>0){
    //     this.bookingsDataFromApi.forEach(element => {
    //       let fullname = element.customerName;
    //       let email = element.email?element.email:"NA";
    //       let phone = element.phoneNum?element.phoneNum:"NA";

    //       if(fullname.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1 || email.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1 ||
    //       phone.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1 ){
    //         localBookingData.push(element);
    //       }
          
    //     });
    //     this.bookingsData = localBookingData;
    //   }else{
    //     this.bookingsData = this.bookingsDataFromApi;

    //   }
      this.searchDecouncer$.next(this.searchText);

  } 


    downloadExcel() {
      const ws: xlsx.WorkSheet =   
      xlsx.utils.table_to_sheet(this.bkgtable.nativeElement);
      const wb: xlsx.WorkBook = xlsx.utils.book_new();
      xlsx.utils.book_append_sheet(wb, ws, 'Sheet1');
      xlsx.writeFile(wb, 'bookingExcel.xlsx');
     }
     onPageChange(event){
      
      console.log(event);
      
      this.currentPage = event-1;
  
      this.getBookingList();
       }
       private setupSearchDebouncer(): void {
        // Subscribe to `searchDecouncer$` values,
        // but pipe through `debounceTime` and `distinctUntilChanged`
        this.searchDecouncer$.pipe(
          debounceTime(500),
          distinctUntilChanged(),
        ).subscribe((term: string) => {
          this.currentPage = 0;
          this.searchText = term;
          this.getBookingList();
        
  
        });
      }
}



